input {
    margin: 1rem;
    height: 3rem;
    min-width: 250px ;
    padding: 0.75rem;
    font-weight: 700;
    color: #3814ff;
    border-radius: 1rem 0.2rem;;
}

.center {
    display: flex;
    justify-content: center;
    flex-direction: column;
    padding: 1rem;
}

li {
    justify-content: center;
    display: flex;
    font-weight: bold;
    font-size: 1.75rem;
    box-shadow: 2px 2px #6b67764d;
}

a {
    list-style: none;

}

.search_field {
    text-align: center;
}

.card {
    background-color: rgb(199, 221, 209);
    margin: 0.1rem;
}